/**
 * Contexte pour l'initialisation d'une view Main d'une source.
 */
export interface ISrcMainCtx extends IRegPointer<IItemTypeUiEnv> {

	shortDesc: JSrcFields

	/** Optimisation : Ã  renseigner si disponible et que l'appelant le sait Ã  jour. */
	longDesc?: JSrcFields

	/** Code spÃ©cial de la mainView Ã  utiliser. */
	mainViewCode?: string

	/** Customisation du registre associÃ© Ã  cette mainView. */
	//configReg?: (reg: IReg<IItemUiEnv>) => void
}


/** Un environnement d'item est issu d'un env d'itemType, avec le securityCtx dÃ©diÃ©, une place enregistrÃ©e et le longDesc de l'item. */
export type IItemUiEnv = IItemTypeUiEnv & {
	srcView?: IView
	longDesc?: JSrcFields
	longDescChange?: EventMgr<(ldNew: JSrcFields, ldOld: JSrcFields) => void>
}

export function isItemUiEnv(env: any): env is IItemUiEnv {return env && env.longDesc != null}

export type ITaskUiEnv = IItemUiEnv & {
	srcView: TaskMain
	itemType: ItemTypeTask
	taskEditBroker?: IInfoBroker
	/** Contenu de la task */
	taskContent?: Document
	/** Etat des champs de la tÃ¢che Ã  son chargement (crÃ©ation de task avec pre-init ou reload de page). */
	initStates?: JTaskStates
}

/**
 * Composant de base pour les mainViews des items.
 */
export interface SrcMain {
	initialize(init: OSrcMainInit): this;
}

export interface OSrcMainInit extends OBaseAreaViewInit<ISrcMainCtx> {}

export abstract class SrcMain extends BaseElementAsync implements IInfoConsumer, IView, IViewsContainer, IShortDescCtx {

	reg: IReg<IItemUiEnv>;
	wsp: Wsp;
	infoBroker: IInfoBroker;

	/** views affichÃ©s dans cette page principale (hors tabs) */
	subViews?: IView[];
	/** Onglets de la view. */
	tabs?: Tabs<SrcMain>;

	async _initialize(init: OSrcMainInit) {
		this.wsp = init.areaContext.reg.env.wsp;
		this.infoBroker = init.areaContext.reg.env.infoBroker;
		if (this.infoBroker) this.infoBroker.addConsumer(this); //on s'abonne de suite pour ne pas louper des demandes de type serchText/searchItemRef, etc.
		this.reg = REG.createSubReg(init.areaContext.reg);
		this.reg.env.place = this.wsp.newPlaceWsp();

		let longDesc = init.areaContext.longDesc;
		if (!longDesc) {
			if (SRC.isNewSrcUri(init.areaContext.shortDesc.srcUri)) {
				longDesc = init.areaContext.shortDesc;
				longDesc.srcRoles = this.reg.env.securityCtx.srcRoles; //objet en crÃ©ation, on rÃ©cupÃ¨re les roles de l'atelier.
			} else {
				longDesc = await this.wsp.fetchLongDesc(SRC.srcRef(init.areaContext.shortDesc));
			}
		}
		//console.log("longDesc:::", longDesc);
		this.reg.env.longDesc = longDesc;
		this.shortDescs[0] = longDesc;
		this.reg.env.srcView = this;
		this.reg.env.longDescChange = new EventMgr();
		this.reg.env.securityCtx = SEC.createSub(this.reg.env.securityCtx, longDesc.srcRoles, longDesc.srcRi, longDesc);

		this.reg.env.itemType.configRegForMainView(init.areaContext, this.reg);
		//if (init.areaContext.configReg) init.areaContext.configReg(this.reg);

		this.reg.env.place.eventsMgr.on("wspUriChange", this.onWspUriChange.bind(this));

		if (ITEM.isExtItem(longDesc.srcUri)) {
			//On Ã©coute les modifs de cet atelier Ã©tranger public.
			this.reg.env.place.getWsp(WSP.extractWspCdFromWspRef(longDesc.itFullUriInOwnerWsp));
		}

		return this._buildContent(init);
	}

	protected async _buildContent(init: OSrcMainInit): Promise<void> {
		this.attachShadow(DOMSH.SHADOWDOM_INIT);
		this.reg.installSkin("webzone:page", this.shadowRoot);
		this.reg.installSkin("wsp-itemmain", this.shadowRoot);
		this._initAndInstallSkin(this.localName, init);
		const itemType = this.reg.env.itemType;
		const mainView = itemType.getDataSrcMainView(init.areaContext);
		const ribRoot = this.shadowRoot.appendChild<HTMLElement>(<header id="ribbons"/>);
		const ribClasses = this.reg.getList<typeof RibbonBase | IArea<IRibbonAreaContext>>("ribbon:item");
		if (ribClasses) {
			const ribbons: RibbonBase[] = [];
			//let resizable = 0;
			for (let i = 0; i < ribClasses.length; i++) {
				//if (i > 0) ribRoot.appendChild(<c-resizer class="ribSep" c-orient="row"/>);

				let rib: RibbonBase;
				if (isArea(ribClasses[i])) {
					const areaCtx: IRibbonAreaContext = {
						reg: this.reg
					};
					if ((ribClasses[i] as IArea).isAvailable(areaCtx)) {
						if ((ribClasses[i] as IArea).needAsync(areaCtx)) {
							rib = ribRoot.appendChild(await (ribClasses[i] as AreaAsync).loadBody(areaCtx, init.lastDatas)) as RibbonBase;
						} else {
							rib = ribRoot.appendChild((ribClasses[i] as IArea).buildBody(areaCtx, init.lastDatas)) as RibbonBase;
						}
					}
				} else
					rib = ribRoot.appendChild(new (ribClasses[i] as typeof RibbonBase)());
				if (rib) {
					ribbons.push(rib);
					//if (rib.hasAttribute("c-resizable")) resizable++;
				}
			}
			// //Au moins 2 ribons resizables pour activer les resizers.
			// if (resizable <= 1) ribRoot.querySelectorAll('c-resizer').forEach((r: Resizer) => r.disabled = true);
			this.subViews = ribbons;
		}

		const converters = itemType.getConvertersForMainView(init.areaContext);
		if (converters) this.reg.addToList("actions:item:burger", "converters", 1, new ItemMainConverters(converters), 100);

		let burgerActions = this.reg.mergeLists<IAction<SrcMain>>("actions:item:burger", "actions:wsp:shortDesc");
		if (burgerActions) {
			burgerActions = ACTION.injectSepByGroup<SrcMain>(burgerActions, "", this);
			ribRoot.insertBefore(<ActionBtn ui-context="bar" Ã®={{
				reg: this.reg,
				action: new ActionMenu<SrcMain>().setActions(burgerActions).setIcon("/@skin@/commons/icons/actions.svg").setLabel("Actions gÃ©nÃ©rales sur l'item"),
				actionContext: this
			} as OActionBtnInit<SrcMain>}/> as ActionBtn<SrcMain>, ribRoot.firstChild);
		}

		if (mainView && mainView.showSsErrors) {
			this.shadowRoot.appendChild<HTMLElement>(new ItemMsgSs().initialize({
				reg: this.reg,
				errors: mainView.showSsErrors.errors
			}));
		}

		let tabs: Area<SrcMain>[];
		if (mainView?.subTabs?.length > 0) {
			const datasTabs = mainView.subTabs; //.filter((tab) => tab.showPerms ? this.reg.hasPerm(tab.showPerms) : true); NON, perms peuvent Ã©voluer, donc affichage en mode tabs
			tabs = [new ItemHomeArea(init).setLabel(mainView.mainTabLabel || "Ãdition").setIcon(mainView.mainTabIcon || "/@skin@/wsp/views/itemMain/penInk.svg")];
			for (const d of datasTabs) tabs.push(new ItemSubTabArea(d, this));
		}
		if (tabs) {
			this.tabs = new Tabs<SrcMain>();
			this.tabs.classList.add("noBorder");
			this.shadowRoot.appendChild(this.tabs.initialize({
				reg: this.reg,
				areasContext: this,
				areas: tabs,
				lastDatasKey: "vtabs",
				lastDatas: init.lastDatas ? init.lastDatas["vtabs"] : null,
				skinOver: "itemMain/c-tabs",
				tabSkinOver: "itemMain/c-tab"
			}));
			await this.tabs.initializedAsync;
			//aprÃ¨s init, on injecte l'enregistrement des houses avant tout changement de tab (pour MÃ j avant init des dynGen en particulier)
			this.tabs.selectTab = async function (this: Tabs<SrcMain>, tab: Tab<SrcMain>, lastDatas?: JLastDatas): Promise<boolean> {
				await REG.findReg<IWspUiEnv>(this).env.universe.wspServer.wspsLive.saveAllHouses();
				return Tabs.prototype.selectTab.call(this, tab, lastDatas);
			};
		} else {
			const body = this.shadowRoot.appendChild(<div id="noTabs"/>);
			if (!this.subViews) this.subViews = [];
			return this._buildHome(init, itemType, body);
		}
	}

	abstract _buildHome(init: OSrcMainInit, itemType: ItemType, parentElt: Node): Promise<void>;

	viewShown: boolean;

	onViewShown() {
		this.viewShown = true;
		if (this.subViews) for (const v of this.subViews) VIEWS.onViewShown(v);
		if (this.tabs) VIEWS.onContainerShown(this.tabs);
	}

	onViewHidden(closed: boolean) {
		this.viewShown = false;
		if (closed) {
			this.reg.env.place.closePlace();
			if (this.infoBroker) this.infoBroker.removeConsumer(this);
		}
		if (this.subViews) for (const v of this.subViews) VIEWS.onViewHidden(v, closed);
		if (this.tabs) VIEWS.onContainerHidden(this.tabs, closed);
	}

	visitViews(visitor: (view: IView) => any, options?: OViewVisitOptions): any {
		if (this.subViews) for (const v of this.subViews) {
			const r = visitor(v);
			if (r !== undefined) return r;
		}
		if (this.tabs) return this.tabs.visitViews(visitor, options);
	}

	async visitViewsAsync(visitor: (view: IView) => Promise<any>, options?: OViewVisitOptions): Promise<any> {
		if (this.subViews) for (const v of this.subViews) {
			const r = await visitor(v);
			if (r !== undefined) return r;
		}
		if (this.tabs) return this.tabs.visitViewsAsync(visitor, options);
	}

	onInfo(info: IInfo): void {}

	/** Construit une view de l'item (editor, dynGen , ...) */
	buildSrcView(v: IDatasSrcView): Promise<IView | null> {
		return v ? newSrcView(v, this.reg) : null;
	}

	async onWspUriChange(msg: JWspUriChangeMsg, from: WspsLivePlace | 'local' | 'server') {
		if (msg.wspCd !== this.reg.env.wsp.code) return;
		const longDesc = this.reg.env.longDesc;
		if (SRC.srcRef(longDesc) === SRC.srcRef(msg) || (msg.type === EWspChangesEvts.perm && SRC.isSubUriOrEqual(msg.srcUri, longDesc.srcUri))) {
			const ldNew = await this.reg.env.wsp.fetchLongDesc(SRC.srcRef(longDesc), this);
			if (!this.isConnected) return;
			if (!this.wsp.isAvailable) await this.wsp.waitForAvailable(this);
			this.reg.env.longDesc = ldNew;
			this.shortDescs[0] = ldNew;
			//console.log("longDesc new:::", ldNew);
			this.reg.env.securityCtx = SEC.createSub(this.reg.env.securityCtx, ldNew.srcRoles, ldNew.srcRi, ldNew);
			this.tabs?.refresh(); //si perms sur tabs
			this.reg.env.longDescChange.emit(ldNew, longDesc);
		}
	}

	protected async focusNodeInEditor(ed: ItemXmlEd, info: InfoFocusNodeInItem) {
		await ed.initializedAsync;
		const wedMgr = ed.view.wedMgr;
		const docHolder = wedMgr.docHolder;
		if (!docHolder) return; //TODO si wedMgr.docHolderAsync uniquement.
		if (typeof info.xpath === "string") {
			const node = docHolder.house.findNodeByXpath(info.xpath);
			if (node) {
				if (wedMgr.drawn) {
					WEDLET.highlightWedletFromLink(XA.from(node), ed.view.wedMgr, 'nearest');
				} else {
					wedMgr.listeners.once("redrawAtEnd", () => {
						WEDLET.highlightWedletFromLink(XA.from(node), ed.view.wedMgr, 'nearest');
					});
				}
			}
		} else {
			if (wedMgr.drawn) {
				WEDLET.highlightWedletFromLink(info.xpath, ed.view.wedMgr, 'nearest');
			} else {
				wedMgr.listeners.once("redrawAtEnd", () => {
					WEDLET.highlightWedletFromLink(info.xpath as IXAddr, ed.view.wedMgr, 'nearest');
				});
			}
		}
	}

	//*** Apis IShortDescCtx ***
	shortDescs: JSrcFields[] = [];

	get emitter() {return this;}
}

