/** Area reprÃ©sentation la home de l'itemMain. */
class ItemHomeArea extends Area<SrcMain> {

	constructor(public init: OSrcMainInit) {
		super('');
	}

	needAsync(ctx: SrcMain): boolean {return true}

	async loadBody(ctx: SrcMain, lastDatas?: JLastDatas): Promise<HTMLElement> {
		const mainTab = <div id="mainTab"/>;
		const init = Object.create(this.init) as OSrcMainInit;
		init.lastDatas = lastDatas;
		await ctx._buildHome(init, ctx.reg.env.itemType, mainTab);
		return mainTab;
	}
}

/** Area de construction des vues secondaires de l'itemMain. */
class ItemSubTabArea extends SimpleTagArea<SrcMain, IView> {
	datas: IDatasItemTab;
	itemMain: SrcMain;

	constructor(datas: IDatasItemTab, itemMain: SrcMain) {
		super(datas.view.tag, null, datas.code);
		this.datas = datas;
		this.itemMain = itemMain;
		this._label = datas.label;
		this._description = datas.description;
		this._icon = datas.icon;
		this._visPerms = datas.showPerms;
	}

	async loadLibs(ctx: SrcMain): Promise<void> {
		const v = this.datas.view;
		if (v.freeLib) await loadFreeLib(ctx.reg.env.itemType.resolvePath(v.freeLib), ctx.reg.env.wsp.wspMetaUi.reg);
		else if (standardSrcViewsLibs[v.tag]) await ctx.reg.env.resolver.importJs(standardSrcViewsLibs[v.tag]);
		return super.loadLibs(ctx);
	}

	needAsync(ctx: SrcMain): boolean {
		if (super.needAsync(ctx)) return true;
		const v = this.datas.view;
		const lib = v.freeLib || standardSrcViewsLibs[v.tag];
		return lib && !customElements.get(v.tag);
	}

	protected newElt(ctx: SrcMain, lastDatas?: JLastDatas): IView {
		const elt = super.newElt(ctx);
		const atts = this.datas.view.atts;
		if (atts) for (const att in atts) elt.setAttribute(att, atts[att]);
		if (elt instanceof BaseElement) {
			const init = BASIS.newInit(this.datas.view.init, this.itemMain.reg);
			init.lastDatas = lastDatas;
			elt.initialize(init);
		}
		return elt;
	}
}


export async function newSrcView(v: IDatasSrcView, reg: IReg<IItemUiEnv>): Promise<HTMLElement> {
	//if (v.showPerm && !reg.hasPerm(v.showPerm)) return null;
	let init = v.init;
	if (v.freeLib) {
		const module = await loadFreeLib(reg.env.itemType.resolvePath(v.freeLib), reg.env.wsp.wspMetaUi.reg);
		if (module.newFreeSrcViewInit) init = module.newFreeSrcViewInit(v, reg);
	}
	if (!customElements.get(v.tag)) {
		const lib = standardSrcViewsLibs[v.tag];
		if (lib) await reg.env.resolver.importJs(lib);
	}
	const view = document.createElement(v.tag) as HTMLElement;
	view.setAttribute("c-resizable", "");
	const atts = v.atts;
	if (atts) for (const att in atts) view.setAttribute(att, atts[att]);
	if (view instanceof BaseElement) {
		view.initialize(BASIS.newInit(init, reg));
		if (isEltInitableAsyncPending(view)) await view.initializedAsync;
	}
	return view;
}


