/**
 * ItemMain pour les itemTypes de la famille "xml".
 */
export class ItemMainXml extends SrcMain {

	mainXmlEd: ItemXmlEd;

	async _buildHome(init: OSrcMainInit, itemType: ItemType, parentElt: Node): Promise<void> {
		const mainView = itemType.getDataSrcMainView(init.areaContext);
		if (mainView && mainView.xmlEditor) {
			this.mainXmlEd = parentElt.appendChild(new ItemXmlEd().initialize({
				reg: this.reg,
				editorKey: mainView.xmlEditor,
				writePerms: "action.itemMainView#edit.content",
				lastDatas: init.lastDatas
			}));
			if (this.subViews) this.subViews.push(this.mainXmlEd);
			return this.mainXmlEd.initializedAsync as unknown as Promise<void>;
		}
	}

	onInfo(info: IInfo): void {
		if (info instanceof InfoFocusNodeInItem) {
			const desc = this.reg.env.longDesc;
			if (desc.srcUri === info.srcRef || desc.srcId === info.srcRef) {
				let ed = this.mainXmlEd;
				if (this.tabs) {
					const currView = this.tabs.selectedTab.view;
					if (currView instanceof ItemXmlEd) {
						ed = currView;
					} else {
						this.tabs.selectTab(this.tabs.getTabByCode(""));
					}
				}
				if (ed) this.focusNodeInEditor(ed, info);
			}
		}
	}
}

