/**
 * ItemMain pour les itemTypes de la famille "res".
 */
export class ItemMainRes extends SrcMain {

	metaXmlEd?: ItemXmlEd;

	protected async _buildContent(init: OSrcMainInit): Promise<void> {
		this.reg.addToList("actions:item:burger", "pasteStream", 1, new PasteContent());
		return super._buildContent(init);
	}

	async _buildHome(init: OSrcMainInit, itemType: ItemType, parentElt: Node): Promise<void> {
		const mainView = itemType.getDataSrcMainView(init.areaContext);
		const resView = await this.buildSrcView(mainView?.resView);
		if (resView) {
			resView.style.flex = "1";
			parentElt.appendChild(resView);
			if (this.subViews) this.subViews.push(resView);
		}
		const editor = mainView?.xmlEditor;
		if (editor) {
			if (resView) parentElt.appendChild(<Resizer c-orient="column"/>);
			const ed = this.metaXmlEd = parentElt.appendChild(<ItemXmlEd id="metaEd" Ã®={{
				reg: this.reg,
				editorKey: editor,
				writePerms: "action.itemMainView#edit.content",
				lastDatas: init.lastDatas
			} as OItemXmlEdInit} c-resizable="" style="display: flex;"/>) as ItemXmlEd; //pour contrer display:contents par dÃ©faut du ItemXmlEd, incompatible avec Resizer.
			if (this.subViews) this.subViews.push(ed);
			await ed.initializedAsync;
			const house = ed.view.wedMgr.docHolder.house as IWspXmlHouse;
			const metasSrcFields = house.srcFields;
			if (metasSrcFields && metasSrcFields.srcSt < 0 && metasSrcFields.srcDt > 0) {
				//MÃ©ta en corbeille Ã  restaurer
				ed.view.wedMgr.setReadOnly("inTrash", true);
				const meatBar = parentElt.insertBefore(<div id="metaInTrashBar">
					<span>Ces mÃ©tadonnÃ©es sont actuellement en corbeille</span>
					<div>
						<Button Ã®={{reg: this.reg, label: "Restaurer", uiContext: "dialog"} as OButtonInit} disabled={this.reg.hasPermission("restore.history", metasSrcFields.srcRoles, undefined, metasSrcFields.srcRi) ? undefined : ""}
										onclick={async (ev: MouseEvent) => {
											await WSP.srcRestore(this.wsp, this, [SRC.srcRef(metasSrcFields)]);
											ed.view.wedMgr.setReadOnly("inTrash", false);
											meatBar.remove();
										}}/>
						<Button Ã®={{reg: this.reg, label: "RÃ©initialiser", uiContext: "dialog"} as OButtonInit}
										disabled={this.reg.hasPermission("action.itemMainView#edit.content", metasSrcFields.srcRoles, undefined, metasSrcFields.srcRi) ? undefined : ""}
										onclick={async (ev: MouseEvent) => {
											await saveDoc(this, house, DOM.newDomDoc(), house.itemType.getModel(), SRC.srcRef(metasSrcFields));
											ed.view.wedMgr.setReadOnly("inTrash", false);
											meatBar.remove();
										}}/>
					</div>
				</div>, ed);
			}
		}
	}

	onInfo(info: IInfo): void {
		if (info instanceof InfoFocusNodeInItem) {
			const desc = this.reg.env.longDesc;
			if (desc.srcUri === info.srcRef || desc.srcId === info.srcRef) {
				if (this.metaXmlEd) this.focusNodeInEditor(this.metaXmlEd, info);
			}
		}
	}

}

