/**
 * ItemMain pour les itemTypes de la famille "task".
 */
export class TaskMain extends SrcMain {

	reg: IReg<ITaskUiEnv>;

	protected startRedraw: Element;
	protected config: OTaskMainInit;

	async redrawMainView(): Promise<void> {
		//cleanup
		this.resetMainView();
		//reconstruction
		await this._buildHome(this.config, this.reg.env.itemType, this.shadowRoot);
		if (this.viewShown) for (const v of this.subViews) VIEWS.onViewShown(v);
	}

	/** UtilisÃ© aprÃ¨s enregistrement d'une tÃ¢che en crÃ©ation pour permettre son remplacement par la tache crÃ©Ã©e*/
	resetMainView() {
		for (const v of this.subViews) VIEWS.onViewHidden(v, true);
		while (this.startRedraw.nextElementSibling) this.startRedraw.nextElementSibling.remove();
		this.subViews.length = 0;
		this.reg.env.initStates = null; // plus d'init avec un Ã©tat issu des lastDatas.
		this.setNeedValid(false);
		(this.reg.env.taskEditBroker as InfoBrokerBasic).removeAllConsumers();
	}

	setNeedValid(needValid: boolean) {
		if (this._needValid !== needValid) {
			this._needValid = needValid;
			if (this.isConnected) this.dispatchEvent(new CustomEvent("wsp-need-valid", {bubbles: true, detail: needValid}));
		}
	}

	protected _needValid: boolean;

	connectedCallback() {
		super.connectedCallback();
		if (this._needValid) this.dispatchEvent(new CustomEvent("wsp-need-valid", {bubbles: true, detail: true}));
	}

	//Pas de tabs secondaires sur les tasks.
	protected async _buildContent(init: OTaskMainInit): Promise<void> {
		this.attachShadow(DOMSH.SHADOWDOM_INIT);
		this.reg.installSkin("webzone:page", this.shadowRoot);
		this._initAndInstallSkin(this.localName, init);
		this.config = init;
		this.startRedraw = this.shadowRoot.lastElementChild;
		this.reg.env.longDescChange.add(async () => {
			//On rÃ©cupÃ¨re le contenu
			await this.fetchContent();
			return this.redrawMainView();
		});
		//console.log("_buildContent init.lastDatas:::", init.lastDatas);
		if (init.lastDatas && (init.lastDatas as JLDTaskMain).tkSt) this.reg.env.initStates = (init.lastDatas as JLDTaskMain).tkSt;
		if (!SRC.isNewSrcUri(this.reg.env.longDesc.srcUri)) {
			await this.fetchContent();
		}
		return this._buildHome(init, this.reg.env.itemType, this.shadowRoot);
	}

	private async fetchContent() {
		if (!this.reg.env.taskContent) this.reg.env.taskContent = DOM.parseDomValid(await WSP.fetchStreamText(this.wsp, this.reg.env.uiRoot, SRC.srcRef(this.reg.env.longDesc)));
	}

	async _buildHome(init: OTaskMainInit, itemType: ItemTypeTask, parentElt: Node) {
		const dataViews = (itemType.datas as IDatasTaskItemType).taskViews;
		const longDesc = this.reg.env.longDesc;
		this.reg.env.taskEditBroker = new InfoBrokerBasic();
		let userNames: string[];
		let view: IDatasTaskView;
		findView: for (const v of dataViews) {
			//Restriction sur l'Ã©tat de la tÃ¢che
			if (v.ifLifeCycleState && !v.ifLifeCycleState.test(longDesc.lcSt)) continue findView;
			//Perm
			if (v.ifPerm && !this.reg.hasPerm(v.ifPerm)) continue findView;
			//Restictions sur l'implication du user dans la tÃ¢che
			if (v.ifInvolvement > 0) {
				if (!longDesc.rspUsrs) continue findView;
				if (!userNames) userNames = await this.reg.env.universe.auth.fetchFlattenedGroups();
				let found: boolean;
				for (const r of longDesc.rspUsrs) {
					if (userNames.indexOf(r.usr) >= 0 && (TASK.findInvolvementForResps(itemType, r.resp, longDesc) & v.ifInvolvement) > 0) {
						found = true;
						break;
					}
				}
				if (!found) continue findView; //Involvement pas trouvÃ©
			}
			//Restictions sur l'app courante
			if (v.ifInApp) {
				const app = DOMSH.findLogicalFlatParentOrSelf(this.reg.env.uiRoot, null, isApp);
				if (!app || !v.ifInApp.test(app.localName)) continue findView; //App pas trouvÃ©
			}
			//Restriction Custom
			if (v.customSelector && !v.customSelector(this.reg, init.viewOptions)) continue findView;
			//Toutes les restrictions sont validÃ©es, on sÃ©lectionne cette view
			view = v;
			break findView;
		}
		if (!view) throw Error("No view found for task: " + longDesc.srcUri);
		//console.log("view::::", view, longDesc);
		const widget = await this.buildSrcView(view.viewDef);
		if (!this.subViews) this.subViews = [widget];
		else this.subViews.push(widget);
		parentElt.appendChild(widget);
		if (this.reg.env.initStates) {
			this.reg.env.taskEditBroker.dispatchInfo(new InfoChangeTask({reg: this.reg, fieldName: 'init'}), this);
		}
	}

	async onWspUriChange(msg: JWspUriChangeMsg, from: WspsLivePlace | 'local' | 'server') {
		if (msg.type === EWspChangesEvts.lcSt) return; //un autre event de type update suis derriÃ¨re... XXX suppr ce double event cotÃ© serveur ?
		const longDesc = this.reg.env.longDesc;
		if (SRC.srcRef(longDesc) === SRC.srcRef(msg)) {
			const ldNew = await this.reg.env.wsp.fetchLongDesc(SRC.srcRef(msg), this);
			if (!this.isConnected) return;
			this.reg.env.longDesc = ldNew;
			this.reg.env.taskContent = null;
			this.shortDescs[0] = ldNew;
			this.reg.env.longDescChange.emit(ldNew, longDesc);
		}
	}


	onViewBeforeHide(close?: boolean): boolean {
		if (close) {
			const editBroker = this.reg.env.taskEditBroker;
			if (editBroker) {
				const st = new InfoReqStateTask();
				editBroker.dispatchInfo(st, this);
				return !st.isDirty;
			}
		}
		return true;
	}

	async onViewWaitForHide(close?: boolean): Promise<boolean> {
		if (close) {
			const editBroker = this.reg.env.taskEditBroker;
			if (editBroker) {
				const st = new InfoReqStateTask();
				editBroker.dispatchInfo(st, this);
				if (st.isDirty) {
					if (SRC.isNewSrcUri(this.reg.env.longDesc.srcUri)) {
						return await POPUP.confirm("Voulez-vous vraiment abandonner cette tÃ¢che en crÃ©ation ?", this, {okLbl: "Abandonner la crÃ©ation"});
					} else {
						return await POPUP.confirm("Cette tÃ¢che est en cours d'Ã©dition, voulez-vous vraiment abandonner vos modifications ?", this, {okLbl: "Abandonner les modifications"});
					}
				}
				return true;
			}
		}
		return true;
	}


	buildLastDatas?(parentLastDatas: JLDTaskMain): void {
		const editBroker = this.reg.env.taskEditBroker;
		if (editBroker) {
			const st = new InfoReqStateTask();
			st.states = {};
			editBroker.dispatchInfo(st, this);
			if (st.isDirty) parentLastDatas.tkSt = st.states;
		}
		//console.log("TaskMain.buildLastDatas:::", JSON.stringify(parentLastDatas));
	}

}

