export async function convertBySwitchTag(ctx: IRegPointer<IItemUiEnv>, props: { tagName: string, tagNs: string, cleanupSchemaItModel?: string }) {
	const env = ctx.reg.env;
	const house = await env.place.getHouse(env.wsp, SRC.srcRef(env.longDesc));
	if (house && isHouseUpdatable(ctx, house)) {
		const doc = house.getDocumentForSave();
		const oldElt = doc.documentElement.firstElementChild;
		const newElt = doc.createElementNS(props.tagNs, props.tagName);
		while (oldElt.hasChildNodes()) newElt.appendChild(oldElt.firstChild);
		oldElt.replaceWith(newElt);
		return saveDoc(ctx, house, doc, props.cleanupSchemaItModel);
	}
}

export async function convertByXsl(ctx: IRegPointer<IItemUiEnv>, props: { xslPath?: string, xsl: string | XSLTProcessor, cleanupSchemaItModel?: string, xslParams: { name: string, value: string }[] }) {
	const env = ctx.reg.env;
	if (!(props.xsl instanceof XSLTProcessor)) {
		const dom = props.xsl ? DOM.parseDom(props.xsl, env.resolver) : await ctx.reg.env.itemType.resolvePath(props.xslPath).fetchDom()
		if (dom.documentElement.firstElementChild.nodeName === "xsl:output") {
			// Les noeuds texte peuvent perturber la normalisation wed du flux XML transformÃ©
			// Cas problÃ©matique : set d'#element avec des #text entre deux => seul le premier est prÃ©servÃ©
			DOM.setAttr(dom.documentElement.firstElementChild, "indent", "no");
		}

		props.xsl = new XSLTProcessor();
		props.xsl.importStylesheet(dom);
	}
	if (props.xsl instanceof XSLTProcessor)
		props.xslParams?.forEach((entry) => (props.xsl as XSLTProcessor).setParameter("", entry.name, entry.value));
	const house = await env.place.getHouse(env.wsp, SRC.srcRef(env.longDesc));
	if (house && isHouseUpdatable(ctx, house)) {
		if (props.cleanupSchemaItModel) {
			const itemType = env.wsp.wspMetaUi.getItemType(props.cleanupSchemaItModel);
			if (itemType) props.xsl.setParameter("", "targetTagName", itemType.getTagRoot());
		}
		return saveDoc(ctx, house, DOM.cleanupDom(props.xsl.transformToDocument(house.getDocumentForSave()), true, false, false), props.cleanupSchemaItModel);
	}
}

function isHouseUpdatable(ctx: IRegPointer<IItemUiEnv>, house: IWspXmlHouse) {
	if (house.lockedBy) {
		POPUP.showNotifForbidden("Cet item est en cours d'Ã©dition, conversion impossible.", ctx.reg.env.uiRoot);
		return false;
	}
	return true;
}

async function saveDoc(ctx: IRegPointer<IItemUiEnv>, house: IWspXmlHouse, doc: Document, cleanupSchemaItModel?: string, resRef?: srcRef) {
	const wasDirty = house.isDirty;
	try {
		house.setDirty(false); //bloque tout save
		const env = ctx.reg.env;
		if (cleanupSchemaItModel) {
			const targetSchema = await env.wsp.wspMetaUi.getItemType(cleanupSchemaItModel)?.getSchema();
			if (targetSchema) {
				targetSchema.bindToDom(doc).correctDocument({autoMutate: true, autoComplete: true, autoCleanup: true, autoNormXml: true, autoNormChars: true})
				targetSchema.fixDocForExport(doc);
			}
		}
		const qs = IO.qs("cdaction", "PutSrc", "param", env.wsp.code, "refUri", resRef || SRC.srcRef(env.longDesc));
		await env.wsp.wspServer.config.wspSrcUrl.fetchVoid(qs, {method: "PUT", body: DOM.ser(doc, true)});
	} finally {
		if (wasDirty) house.setDirty(true);
	}
}


