/**
 * Ajoute les listeners pour autoriser le drag et le drop d'un fichier sur une srcView
 *
 * Remarque : le plugin PDF natif de Chrome intercepte les Ã©vÃ¨nements de drag
 *
 * @param reg
 * @param srcView Le widget Ã  augmentÃ© avec le drop, doit avoir un shadow attachÃ©
 * @param callback Fonction a appelÃ© sur le drop, remplace le flux principal si non prÃ©cisÃ©
 *
 */
export function installSrcViewFileDrop(reg: IReg<IItemUiEnv>, srcView: BaseElement, callback?: (entry: DataTransferItem) => void) {
	reg.installSkin("srcview/filedrop", srcView.shadowRoot);

	const {env} = reg;

	const dropLayer = <div class="fileDropLayer">Importez le fichier en le dÃ©posant ici.</div>;

	srcView.addEventListener('dragenter', function (ev: DragEvent) {
		const item = ev.dataTransfer.items[0];
		if (item && item.kind == 'file' && env.itemType.matchType(item.type) > 1) {
			this.shadowRoot.appendChild(dropLayer);
		}
	});

	srcView.addEventListener('dragover', function (ev: DragEvent) {
		if (dropLayer.isConnected) {
			ev.preventDefault();
			ev.stopPropagation();
		}
	});

	srcView.addEventListener('dragleave', function () {
		if (dropLayer.isConnected) this.shadowRoot.removeChild(dropLayer);
	});

	srcView.addEventListener('drop', async function (ev: DragEvent) {
		this.shadowRoot.removeChild(dropLayer);
		const item = ev.dataTransfer.items[0];
		if (callback) {
			callback(item);
		} else {
			const progress = POPUP.showProgress(srcView, "Import en cours...");
			try {
				await ITEM.update(env.wsp, env.uiRoot, SRC.srcRef(env.longDesc), item.getAsFile(), progress.progressCallback);
				progress.close();
			} catch (e) {
				progress.close();
				await ERROR.report("Une erreur est survenue lors de l'import de la ressource.", e, reg);
			}
		}
	});
}


